.news {
    margin-bottom: 0.45em;
}
a {
    color: #8ab1ff;
    text-decoration: none;
    font-weight: bold;
    padding-top: 0.20em;
    padding-bottom: 0.20em;
    font-size: 11.5pt;
}

.tags {
    margin-right: 0.25em;
    background-color: #3b320d;
    border: 1px solid #665501;
    border-radius: 5px;
    color: rgb(225 225 225 / 87%);
    font-size: 8pt;
    margin-left: 0.30em;
    padding: 0px 0.4em 1px 0.4em;
    text-decoration: none;
}
.source {
    color: rgb(225 225 225 / 61%);
    font-style: italic;
    font-size: 9pt;
    text-decoration: none;
    vertical-align: middle;
}
.infos {
    color: rgb(225 225 225 / 61%);
    font-size: 9pt;
}

/*Make it smooth when it disapears*/

.tile {
    transition: all 0.5s ease-in-out;
}
